import { Injectable } from '@angular/core';
import {Siteuser} from "./siteuser";
import {Http, Response} from '@angular/http';
import {promise} from "selenium-webdriver";

@Injectable({
  providedIn: 'root'
})
export class SiteuserServiceService {

  private userUrl = 'http://localhost:3000/api/users';

  constructor(private http: Http) { }
  getUsers(): Promise<void | Siteuser[]> {
    return this.http.get(this.userUrl)
      .toPromise()
      .then(response => response.json() as Siteuser[])
      .catch(this.handleError);
  }
  getSingleUser(userid: string): Promise<void | Siteuser>{
    return this.http.get(this.userUrl + '/' + userid)
      .toPromise()
      .then(response => response.json() as Siteuser)
      .catch(this.handleError);
  }

  createNewUser(newUser: Siteuser) : Promise<void | Siteuser>{
    return this.http.post(this.userUrl, newUser)
      .toPromise()
      .then(response => response.json() as Siteuser)
      .catch(this.handleError)

  }

  movieUpdate(newUser: Siteuser): Promise<void | Siteuser>{

    return this.http.put(this.userUrl + '/' + newUser._id, newUser)
      .toPromise()
      .then(response => response.json() as Siteuser)
      .catch(this.handleError);
  }

  movieDelete(userid: string) : Promise<void | string>{
    return this.http.delete(this.userUrl + '/' + userid)
      .toPromise()
      .then(response => response.json() as string)
      .catch(this.handleError);

  }

  private handleError(error: any){
    console.log("error");
  }
}
